
html {
  font-size: 100px;
}
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 60px;
  color: #2c3e50;
  background: #fafafa;
}
/* reset css */
html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, a, cite, img, sub, sup, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  border: 0;
  margin: 0;
  padding: 0;
  font-weight: normal;
  vertical-align: baseline;
}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}
button {
  outline: none;
}
ol, ul {
  margin: 0;
  padding: 0;
}
ol, ul, li {
  list-style: none;
}
i, em {
  font-style: normal;
}
blockquote {
  quotes: none;
}
blockquote:before, blockquote:after {
  content: '';
  content: none;
}
a {
  outline: none;
  text-decoration: none;
}
input, textarea{
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  outline: none;
  border: 0;
  margin: 0;
  padding: 0;
}
select, option{
  font-family: inherit;
}
*::before,
*::after,
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.clearfix::after {
  content: "";
  display: table;
  clear: both;
  float: none;
  *zoom: 1;
}
.clear-box {
  display: table;
  clear: both;
  float: none;
  *zoom: 1;
}
.kakushi {
  position: absolute;
  -webkit-filter: opacity(0);
          filter: opacity(0);
  clip: rect(0,0,0,0);
  z-index: -99;
}
@-webkit-keyframes rotateY {
from {-webkit-transform: rotateY(0deg);transform: rotateY(0deg);
}
to   {-webkit-transform: rotateY(360deg);transform: rotateY(360deg);
}
}
@keyframes rotateY {
from {-webkit-transform: rotateY(0deg);transform: rotateY(0deg);
}
to   {-webkit-transform: rotateY(360deg);transform: rotateY(360deg);
}
}
@-webkit-keyframes moveBG {
from {background-position: 0% 0%;
}
to   {background-position: -400% -200%;
}
}
@keyframes moveBG {
from {background-position: 0% 0%;
}
to   {background-position: -400% -200%;
}
}
@-webkit-keyframes slantMove {
0% {-webkit-transform: translate3d(0, 0, 0);transform: translate3d(0, 0, 0);
}
25% {-webkit-transform: translate3d(-5%, -50%, 0);transform: translate3d(-5%, -50%, 0);
}
50% {-webkit-transform: translate3d(-30%, -65%, 0);transform: translate3d(-30%, -65%, 0);
}
75% {-webkit-transform: translate3d(-55%, -35%, 0);transform: translate3d(-55%, -35%, 0);
}
100%   {-webkit-transform: translate3d(-75%, -50%, 0);transform: translate3d(-75%, -50%, 0);
}
}
@keyframes slantMove {
0% {-webkit-transform: translate3d(0, 0, 0);transform: translate3d(0, 0, 0);
}
25% {-webkit-transform: translate3d(-5%, -50%, 0);transform: translate3d(-5%, -50%, 0);
}
50% {-webkit-transform: translate3d(-30%, -65%, 0);transform: translate3d(-30%, -65%, 0);
}
75% {-webkit-transform: translate3d(-55%, -35%, 0);transform: translate3d(-55%, -35%, 0);
}
100%   {-webkit-transform: translate3d(-75%, -50%, 0);transform: translate3d(-75%, -50%, 0);
}
}

