.oo-arrow{position:absolute;top:50%;right:.16rem;width:.1rem;height:.1rem;-webkit-transform:translateY(-50%);transform:translateY(-50%)
}
.oo-arrow::before,.oo-arrow::after{content:'';position:absolute;top:.04rem;left:.04rem;vertical-align:baseline;background:#666;width:.06rem;height:.02rem;border-radius:2px;-webkit-transition:all 0.34s cubic-bezier(0.645, 0.045, 0.355, 1);transition:all 0.34s cubic-bezier(0.645, 0.045, 0.355, 1);-webkit-transform-origin:16.7% 50%;transform-origin:16.7% 50%
}
.oo-arrow::before{-webkit-transform:rotate(45deg);transform:rotate(45deg)
}
.oo-arrow::after{-webkit-transform:rotate(135deg);transform:rotate(135deg)
}
.oo-ck{position:absolute;clip:rect(0, 0, 0, 0)
}
.oo-ck:checked+.oo-menu-group .oo-arrow::before{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)
}
.oo-ck:checked+.oo-menu-group .oo-arrow::after{-webkit-transform:rotate(225deg);transform:rotate(225deg)
}
.oo-ck:checked+.oo-menu-group .oo-menu-item a{height:0;margin-bottom:0
}

