.puzzle-wrapper .form .group>span.icon.error{color:#f56c6c
}
.puzzle-wrapper .form .group>span.icon .svg-icon{position:absolute;top:100%;-webkit-transform:translate(0, -100%);transform:translate(0, -100%);cursor:pointer
}
.puzzle-wrapper .form .type-content .type-item{width:25%;min-width:1rem;float:left;padding:.1rem;color:#0af;font-size:.12rem;-webkit-filter:grayscale(100%);filter:grayscale(100%);-webkit-transition:all .34s;transition:all .34s
}
.puzzle-wrapper .form .type-content .type-item.selected{-webkit-filter:grayscale(0%);filter:grayscale(0%)
}
.puzzle-wrapper .form .type-content .type-item span{white-space:nowrap
}
.puzzle-wrapper.side .preview{float:left;width:40%;height:100%;padding:0 1em
}
.puzzle-wrapper.side .preview::after{content:none
}
.puzzle-wrapper.side .preview .content{width:100%
}
.puzzle-wrapper.side .preview .content::after{margin-bottom:92.7%
}
.puzzle-wrapper.side .config{overflow:hidden;border:none;margin:0;padding:0 1em 1em 0
}
.puzzle-wrapper .tabs{position:relative;display:inline-block;width:100%;font-size:.14rem;border-bottom:2px solid #ccc;margin-bottom:.5em
}
.puzzle-wrapper .tabs .tab{float:left;height:2em;padding:0 1em;-webkit-transition:all .34s;transition:all .34s
}
.puzzle-wrapper .tabs .tab.select{color:#039be5
}
.puzzle-wrapper .tabs .bar{position:absolute;bottom:-2px;height:2px;min-width:3em;background:#4af;-webkit-transition:all .34s;transition:all .34s
}

.legend{font-size:.14rem
}
.legend .line{padding:0 1em .5em;border-bottom:1px solid #ddd
}
.legend .radio-group-wrapper{position:relative;top:-.08rem
}
.legend .radio-group-wrapper,.legend .o-switch{float:right;margin-left:.06rem
}
